import React from 'react'
import "./login.css"
function Login() {
    return (
        <div className="login">

            <div className="login_container">
                <h1>Sign In</h1>
                <form>
                    <h5>E-mail</h5>
                    <input type = "text"/>
                    <h5>Password</h5>
                    <input type = "password"/>
                    
                </form>

                <p>By continuing, you agree to Amazon's Conditions of Use and Privacy Notice.</p>
                <div>
                    <button className = "login_registerButton">Create a new User</button>
                </div>
            </div>
            
        </div>
    )
}

export default Login
